/* === Week View Styles === */
.week-view-container {
    overflow-x: auto;
    position: relative;
    transition: background-color 0.3s ease;
  }
  .dark .week-view-container {
    background-color: #1f2937; /* gray-800 */
  }
  
  .week-header {
    display: flex;
    position: sticky;
    top: 0;
    background-color: #f9fafb;
    z-index: 10;
  }
  .dark .week-header {
    background-color: #111827; /* gray-900 */
  }
  
  .day-header {
    flex: 1;
    text-align: center;
    padding: 8px;
    font-weight: bold;
    border-bottom: 1px solid #ccc;
    background-color: #fff;
    color: #111827;
    transition: background-color 0.3s, color 0.3s;
  }
  .dark .day-header {
    background-color: #374151; /* gray-700 */
    color: #f3f4f6; /* gray-100 */
  }
  
  .day-header.today {
    background-color: #e0f7fa;
    color: #0077b6;
  }
  .dark .day-header.today {
    background-color: #0f172a; /* darker blue for highlight */
    color: #38bdf8; /* cyan-400 */
  }
  
  .time-label-cell {
    width: 60px;
  }
  
  .week-grid {
    display: flex;
    flex-direction: column;
    height: 80vh;
    overflow-y: auto;
    position: relative;
  }
  
  .time-row {
    display: flex;
    border-top: 1px solid #eee;
  }
  .dark .time-row {
    border-top: 1px solid #374151;
  }
  
  .time-label {
    width: 60px;
    padding: 4px;
    text-align: right;
    font-size: 0.8rem;
    color: #666;
    background-color: #f1f5f9;
    transition: background-color 0.3s, color 0.3s;
  }
  .dark .time-label {
    color: #d1d5db;
    background-color: #1f2937;
  }
  
  .time-slot {
    flex: 1;
    border-left: 1px solid #eee;
    height: 40px;
    cursor: pointer;
    background-color: white;
    position: relative;
    transition: background-color 0.3s;
  }
  .dark .time-slot {
    background-color: #1f2937;
    border-left: 1px solid #374151;
  }
  .time-slot:hover {
    background-color: #e0f2fe;
  }
  .dark .time-slot:hover {
    background-color: #334155; /* gray-700 hover */
  }
  
  .event-column {
    position: absolute;
    top: 0;
    left: 60px;
    right: 0;
    display: flex;
    pointer-events: none;
  }
  
  .event-column > .event-block {
    position: absolute;
    left: 0;
    right: 0;
    margin: 2px 4px;
    padding: 4px 6px;
    font-size: 0.8rem;
    border-radius: 6px;
    color: white;
    pointer-events: auto;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  
  /* Event text details */
  .event-block .event-time-range {
    font-size: 0.75rem;
    opacity: 0.9;
    margin-bottom: 2px;
  }
  .event-block .event-title {
    font-weight: bold;
    font-size: 0.85rem;
  }
  
  /* Reuse event colors */
  .event-blue { background-color: #3b82f6; }
  .event-red { background-color: #ef4444; }
  .event-green { background-color: #10b981; }
  .event-yellow { background-color: #f59e0b; }
  .event-purple { background-color: #8b5cf6; }
  .event-indigo { background-color: #6366f1; }
  .event-pink { background-color: #ec4899; }
  .event-teal { background-color: #14b8a6; }
  
  /* Scrollbar styles */
  .week-grid::-webkit-scrollbar {
    width: 8px;
  }
  .week-grid::-webkit-scrollbar-thumb {
    background: #cbd5e0;
    border-radius: 4px;
  }
  .dark .week-grid::-webkit-scrollbar-thumb {
    background: #4b5563;
  }
  